import React, { useState, useEffect } from "react";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import { List, ListItemAvatar, Avatar, ListItemText, makeStyles, ListItem } from "@material-ui/core";

export const SearchForm = () => {
    const [key, setKey] = useState()
    const [data, setData] = useState([])
    const useStyle = makeStyles((theme)=>({
        root:{
            position:'absolute',
            top:'100%',
            zIndex:1050,
            width:'100%',
            background:'white'
        },
        pic:{
            height:56,
            width:56
        },
        inline:{
            display:'inline',
            marginLeft:8
        }
    }))
    const classes = useStyle()
    let searchTimeOut
    const search = async (key) =>{
        const {data} = await Axios.get(`${nodeApiLink}/seach/${key}`)
        setData(data)
    }
    useEffect(() => {
        searchTimeOut && clearTimeout(searchTimeOut)
        searchTimeOut = setTimeout(()=>{
            search(key)
        },1000)
        return () => {
            //
        }
    }, [key])
    return (
        <form className="seach-form d-inline-flex align-content-center">
            <input type="text" name="" placeholder="Tìm kiếm..." value={key} onChange={(e)=>{setKey(e.target.value)}}/>
            <button type="submit">
                <i className="fas fa-search"></i>
            </button>
            {/* <List className={classes.root} >
                <ListItem>
                    <ListItemAvatar >
                    <Avatar src='/Images/pic1.jpg' variant='square' className={classes.pic} />
                </ListItemAvatar>
                <ListItemText className={classes.inline} primary='sfdfdffs' secondary='dfsfsdfsdf'>

                </ListItemText>
                </ListItem>
                
            </List> */}
        </form>
    );
};
